import React from 'react';
import { Navegacao } from '../components/index'
import CardsProfile from '../components/CardsProfile';
import { CardDeck } from 'react-bootstrap';

// import { Container } from './styles';

function Sobre() {

  // ### Substituir aqui pelo endpoint do firebase  ###
  // GET [https://carro-eletrico.firebaseio.com/autores.json] 
  let profiles = [
    {
      "nome": "Paulão Teste",
      "img": "data:image/jpeg;base64,",
      "descricao": "Respira front, atualmente se alimenta de full-stack."
    },
    {
      "nome": "Silvião Teste",
      "img": "https://avatars3.githubusercontent.com/u/40775733?s=460&u=8302c36188b205779706c034022bec08db98405c&v=4",
      "descricao": "Programa, joga tênis e aceita Cheques."
    }
  ]

  return (
    <>
      <Navegacao></Navegacao>
      <h1>Sobre o projeto</h1>
      <p>
        Poucos são os proprietários de veículos elétricos no Brasil. No entento, este número tende a 
        crescer nas próximas décadas. Esta iniciativa visa facilitar a busca por unidades de abastecimento
        preparadas para receber veículos elétricos no estado de São Paulo.  
      </p>
      <h1>Sobre os autores</h1>
      <CardDeck>
        {profiles.map(autor => ( 
          <CardsProfile  profile_info={autor} />
        ))}
      </CardDeck>
    </>
  );
}

export default Sobre;